import React, { useCallback, useState } from 'react'

import IconText from './components/IconText'
import { ReactComponent as ImportIcon } from 'assets/icon/import.svg'
import { ReactComponent as LayoutIcon } from 'assets/icon/layout.svg'
import { ReactComponent as AiIcon } from 'assets/icon/ai.svg'
import { ReactComponent as ThreeDIcon } from 'assets/icon/3d.svg'
import { ReactComponent as AnalyticsIcon } from 'assets/icon/analytics.svg'
import { ReactComponent as MoreIcon } from 'assets/icon/more.svg'
import { ReactComponent as FleshRemovalIcon } from 'assets/icon/fleshremoval.svg'
import { Box, Menu, MenuItem } from '@material-ui/core'
import XrayTool from './components/XrayTool'
import { Layout } from 'common/utils'
import AiModelTabs from './components/AiModelTabs'
import { Plugin } from 'common/types'

const MENU_ITEMS: Layout[] = ['OneXOne', 'OneXTwo', 'TwoXOne', 'TwoXTwo']

interface Props {
  layout: Layout
  currentPlugin: Plugin | null
  onChangeLayout: (newLayout: Layout) => void
  onSwitchPlugin: (plugin: Plugin) => void
}

export default function Toolbar(props: Props) {
  const { layout, currentPlugin, onChangeLayout, onSwitchPlugin } = props
  const [showLayoutMenu, toggleLayoutMenu] = useState(false)
  const [menuAnchor, setMenuAnchor] = useState<null | HTMLElement>(null)

  const onSwitchLayout = useCallback(
    (item: Layout) => {
      onChangeLayout(item)
      toggleLayoutMenu(false)
    },
    [onChangeLayout]
  )

  return (
    <>
      <Box display="flex" justifyContent="space-between" pl="7.5px" pr="20px">
        {/* Basic dicom functionality */}
        <Box display="flex">
          <XrayTool />
          <IconText icon={<ImportIcon />} text="Import" type="primary" />
          <IconText
            icon={<LayoutIcon />}
            text="Layout"
            type="primary"
            onClick={(event: React.MouseEvent<HTMLElement>) => {
              setMenuAnchor(event.currentTarget)
              toggleLayoutMenu(true)
            }}
          />
        </Box>

        {/* Plugin and setting */}
        <Box display="flex" justifyContent="flex-end">
          <IconText
            icon={<AiIcon />}
            text="AI"
            type="plugin"
            active={currentPlugin === 'ai'}
            onClick={() => onSwitchPlugin('ai')}
          />
          <IconText icon={<ThreeDIcon />} text="3D" type="plugin" />
          <IconText
            icon={<FleshRemovalIcon />}
            text="Flesh removal"
            type="plugin"
            active={currentPlugin === 'fleshRemoval'}
            onClick={() => onSwitchPlugin('fleshRemoval')}
          />
          <IconText icon={<AnalyticsIcon />} text="Analytics" type="plugin" />
          <IconText icon={<MoreIcon />} text="Settings" type="plugin" />
        </Box>
      </Box>
      {currentPlugin === 'ai' && <AiModelTabs />}
      <Menu
        id="simple-menu"
        anchorEl={menuAnchor}
        open={showLayoutMenu}
        onClose={() => toggleLayoutMenu(false)}
      >
        {MENU_ITEMS.map((item) => (
          <MenuItem key={item} selected={layout === item} onClick={() => onSwitchLayout(item)}>
            {item}
          </MenuItem>
        ))}
      </Menu>
    </>
  )
}
