import { Box, Dialog, Grid } from '@material-ui/core'
import { OAResult } from 'common/types'
import { FILES_URL } from 'common/utils'
import { useSummaryStyles } from 'PatientDetail/styles'
import { useState } from 'react'

function gradeColor(grade: number) {
  if (grade < 1) {
    return '#FFFFFF'
  } else if (grade < 2) {
    return '#FFB800'
  }
  return '#FF0000'
}

const Detected: React.FC<{ detected: boolean }> = ({ detected }) => (
  <span className={detected ? 'highlight' : ''}>{detected ? 'Detected' : 'No'}</span>
)

const Image: React.FC<{ img: string; alt: string; onClick: (img: string) => void }> = ({
  img,
  alt,
  onClick,
}) => <img style={{ cursor: 'pointer' }} src={img} alt={alt} onClick={() => onClick(img)} />

const CustomGrid: React.FC<{ className: string; rightKnee: any; label: any; leftKnee: any }> = ({
  className,
  rightKnee,
  label,
  leftKnee,
}) => (
  <Grid container={true} alignItems="center" className={className}>
    <Grid className="left" item={true} xs container={true} justify="flex-end">
      {rightKnee}
    </Grid>
    <Grid className="label" item={true} xs={3} container={true} justify="center">
      {label}
    </Grid>
    <Grid className="right" item={true} xs container={true} justify="space-between">
      {leftKnee}
    </Grid>
  </Grid>
)

interface Props {
  data: OAResult
  withThumbnail: boolean
}

export default function OASummary(props: Props) {
  const {
    data: { right, left },
    withThumbnail,
  } = props
  const classes = useSummaryStyles({ withThumbnail })
  const [currentImg, setCurrentImg] = useState<string | null>(null)

  const customGridClasses = `${classes.customGrid} ${withThumbnail ? '' : 'small'}`

  return (
    <div className={classes.summary} style={{ height: withThumbnail ? '90%' : '100%' }}>
      <div className={classes.title}>Knee Osteoarthritis AI Labeling Summary</div>

      <section className={classes.section}>
        <CustomGrid
          className={`${customGridClasses} ${classes.direction}`}
          rightKnee="Right"
          label=""
          leftKnee="Left"
        />

        {/* KL Grade */}
        <CustomGrid
          className={`${customGridClasses} ${classes.klGrade}`}
          rightKnee={
            <span
              style={{ fontSize: '30px', lineHeight: '40.8px', color: gradeColor(right.klGrade) }}
            >
              {right.klGrade}
            </span>
          }
          label={<span style={{ fontSize: '24px', lineHeight: '30.7px' }}>KL Grade</span>}
          leftKnee={
            <span
              style={{ fontSize: '30px', lineHeight: '40.8px', color: gradeColor(left.klGrade) }}
            >
              {left.klGrade}
            </span>
          }
        />

        {/* Joint space width */}
        <div
          style={{
            fontSize: '20px',
            lineHeight: '27px',
            marginBottom: '30px',
            textAlign: 'center',
          }}
        >
          Joint space width
        </div>
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" flex="1">
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${right.jsw.filename}`}
                  alt="JSN"
                  onClick={setCurrentImg}
                />
              )}
              <Box display="flex" justifyContent="space-between" flexDirection="column">
                <div>{right.jsw.raw}</div>
                <div>{right.jsw.standardized}</div>
              </Box>
            </Box>
          }
          label={
            <Box display="flex" justifyContent="space-between" flexDirection="column" height="100%">
              <div className="gray">RAW</div>
              <div className="gray">Standardized</div>
            </Box>
          }
          leftKnee={
            <Box display="flex" justifyContent="space-between" flex="1">
              <Box display="flex" justifyContent="space-between" flexDirection="column">
                <div>{left.jsw.raw}</div>
                <div>{left.jsw.standardized}</div>
              </Box>
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${left.jsw.filename}`}
                  alt="JSN"
                  onClick={setCurrentImg}
                />
              )}
            </Box>
          }
        />

        {/* Osteophyte */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image img={`${FILES_URL}/${right.op.filename}`} alt="OP" onClick={setCurrentImg} />
              )}
              <Detected {...right.op} />
            </Box>
          }
          label="Osteophyte"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <Detected {...left.op} />
              {withThumbnail && (
                <Image img={`${FILES_URL}/${left.op.filename}`} alt="OP" onClick={setCurrentImg} />
              )}
            </Box>
          }
        />

        {/* Sclerosis */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${right.sclerosis.filename}`}
                  alt="SC"
                  onClick={setCurrentImg}
                />
              )}
              <Detected {...right.sclerosis} />
            </Box>
          }
          label="Sclerosis"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <Detected {...left.sclerosis} />
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${left.sclerosis.filename}`}
                  alt="SC"
                  onClick={setCurrentImg}
                />
              )}
            </Box>
          }
        />

        {/* Alignment */}
        <CustomGrid
          className={`${customGridClasses} ${classes.resultRow}`}
          rightKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${right.alignment.filename}`}
                  alt="Alignment"
                  onClick={setCurrentImg}
                />
              )}
              <span>{right.alignment.angle} </span>
            </Box>
          }
          label="Alignment"
          leftKnee={
            <Box display="flex" justifyContent="space-between" alignItems="center" flex="1">
              <span>{left.alignment.angle} </span>
              {withThumbnail && (
                <Image
                  img={`${FILES_URL}/${left.alignment.filename}`}
                  alt="Alignment"
                  onClick={setCurrentImg}
                />
              )}
            </Box>
          }
        />
      </section>

      {currentImg && (
        <Dialog
          open={true}
          onClose={() => setCurrentImg(null)}
          maxWidth="md"
          classes={{ paper: classes.dialog }}
        >
          <img src={currentImg} alt="img" />
          <div className="close-btn" onClick={() => setCurrentImg(null)}>
            X
          </div>
        </Dialog>
      )}
    </div>
  )
}
