import { useEffect, useRef, useState } from 'react'

import * as cornerstone from 'cornerstone-core'
import * as cornerstoneTools from 'cornerstone-tools'
import { globalSubject } from 'common/utils'
import { SubjectTopic } from 'common/utils'
import Bone from './bone.png'
import BoneAlpha5 from './bone_alpha_5.png'
import { fromEvent } from 'rxjs'
import { mergeMap, map, takeUntil } from 'rxjs/operators'

const FleshRemoval: React.FC = () => {
  const [opacity, setOpacity] = useState(1)
  const divRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!divRef.current) return

    const mouseDowns = fromEvent<MouseEvent>(divRef.current, 'mousedown')
    const mouseUps = fromEvent<MouseEvent>(divRef.current, 'mouseup')
    const mouseMoves = fromEvent<MouseEvent>(window.document, 'mousemove')

    mouseDowns
      .pipe(
        mergeMap((mouseDown) =>
          mouseMoves.pipe(
            map((mouseMove) => mouseMove.clientY - mouseDown.clientY),
            takeUntil(mouseUps.pipe())
          )
        )
      )
      .subscribe((delta) => {
        setOpacity((prev) => {
          const nextOpacity = prev + delta * 0.0001
          return nextOpacity <= 0 ? 0 : nextOpacity >= 1 ? 1 : nextOpacity
        })
      })
  }, [])

  return (
    <div ref={divRef} draggable={false}>
      <img
        draggable={false}
        src={Bone}
        alt="bone"
        style={{
          position: 'absolute',
          top: 0,
          left: 0,
          width: '100%',
          height: '100%',
          zIndex: 10,
          opacity,
        }}
      />
      <img
        draggable={false}
        src={BoneAlpha5}
        alt="alpha5"
        style={{ position: 'absolute', top: 0, left: 0, width: '100%', height: '100%', zIndex: 9 }}
      />
    </div>
  )
}

interface Props {
  id: string
  filepath: string
  enableFleshRemoval: boolean
}

export default function ImageViewer(props: Props) {
  const { id, filepath, enableFleshRemoval } = props
  const elementRef = useRef(null)

  useEffect(() => {
    cornerstone.enable(elementRef.current!)
    const imageIds = [`wadouri:${filepath}`]

    const stack = {
      currentImageIdIndex: 0,
      imageIds: imageIds,
    }

    cornerstone.loadImage(imageIds[0]).then((image: any) => {
      cornerstoneTools.addStackStateManager(elementRef.current!)
      cornerstoneTools.addToolState(elementRef.current!, 'stack', stack)
      cornerstone.displayImage(elementRef.current!, image)
      globalSubject.next(SubjectTopic.InitXrayTool)
    })
  }, [filepath])

  return (
    <div
      ref={elementRef}
      id={id}
      style={{
        maxWidth: '100%',
        height: '100%',
        backgroundColor: '#000000',
        outline: 'none',
      }}
      tabIndex={0}
      onContextMenu={(e) => {
        e.preventDefault()
      }}
    >
      {enableFleshRemoval && <FleshRemoval />}
    </div>
  )
}
