import { makeStyles, Theme } from '@material-ui/core'
import { ReactComponent as KneelIcon } from 'assets/icon/knee.svg'
import { ReactComponent as HipIcon } from 'assets/icon/hip.svg'
import { ReactComponent as SpineIcon } from 'assets/icon/spine.svg'
import { ReactComponent as ShoulderIcon } from 'assets/icon/shoulder.svg'
import SummaryImg from 'assets/img/summary.png'
import HistoryImg from 'assets/img/history.png'
import ThreeDLeftImg from 'assets/img/3d-left.png'
import ThreeDRightImg from 'assets/img/3d-right.png'
import React, { useCallback, useState } from 'react'
import { globalSubject, parseImageAndInferenceItem, SubjectTopic } from 'common/utils'
import { ModelFunc, Study } from 'common/types'
import { useQueryParams } from 'common/hooks'
import { GET_STUDIES } from 'PatientDetail/gql'
import { client } from 'common/apolloClient'

const useStyles = makeStyles((theme: Theme) => ({
  '@keyframes showUp': {
    from: {
      height: '0px',
    },
    to: {
      height: '107px', // tabs + funcTabs
    },
  },
  root: {
    animation: `$showUp 500ms ${theme.transitions.easing.easeInOut}`,
  },
  tabs: {
    display: 'flex',
    height: '32px',
    background: 'rgba(19, 156, 255, 0.1)',
    padding: '0px 6px',
    marginBottom: '7px',
  },
  tab: {
    minWidth: '76px',
    fontSize: '13px',
    lineHeight: '18px',
    height: '28px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    marginRight: '18px',
    '&:hover': {
      color: '#FFFFFF !important',
      cursor: 'pointer',
    },
  },
  funcTabs: {
    display: 'flex',
    height: '75px',
    paddingLeft: '24px',
  },
  funcTab: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
    marginRight: '48px',
    fontSize: '12px',
    lineHeight: '14px',
    color: 'rgba(255, 255, 255, 0.7)',
  },
}))

const Tab: React.FC<{
  className: string
  icon: React.ReactElement
  text: string
  active: boolean
  onClick: () => void
}> = ({ className, icon, text, active, onClick }) => {
  return (
    <div
      className={className}
      onClick={onClick}
      style={{
        color: active ? '#FFFFFF' : 'rgba(255, 255, 255, 0.7)',
        borderBottom: active ? 'solid 4px #C53652' : 'none',
      }}
    >
      {icon}
      {text}
    </div>
  )
}

const ImgText: React.FC<{
  className: string
  img: string
  text: string
  active: boolean
  onClick: () => void
}> = ({ className, img, text, active, onClick }) => {
  return (
    <div
      className={className}
      style={{
        border: active ? '1px solid #C53652' : 'none',
        borderRadius: '3px',
      }}
      onClick={onClick}
    >
      <img src={img} alt={text} />
      <span style={{ transform: 'scale(0.83)' }}>{text}</span>
    </div>
  )
}

export default function AiModelTabs() {
  const classes = useStyles()
  const [category, setCategory] = useState('knee')
  const [func, setFunc] = useState<ModelFunc | null>(null)
  const studyIds = useQueryParams('studyIds').split(',')
  const data = client.readQuery<{ studies: Study[] }, { ids: string[] }>({
    query: GET_STUDIES,
    variables: { ids: studyIds },
  })

  const onSwitchFunc = useCallback(
    (newFunc: ModelFunc) => {
      data?.studies.forEach((study) => {
        study.series.forEach(({ inference }) => {
          inference.forEach((inf) => {
            if (inf.func === newFunc) {
              globalSubject.next(SubjectTopic.SwitchFunc, {
                func: newFunc,
                item: parseImageAndInferenceItem(inf),
              })
              return
            }
          })
        })
      })
      setFunc(newFunc)
    },
    [data]
  )

  return (
    <div className={classes.root}>
      <section className={classes.tabs}>
        <Tab
          className={classes.tab}
          icon={<KneelIcon />}
          text="Knee"
          active={category === 'knee'}
          onClick={() => setCategory('knee')}
        />
        <Tab
          className={classes.tab}
          icon={<HipIcon />}
          text="Pelvis & Hip"
          active={category === 'hip'}
          onClick={() => setCategory('hip')}
        />
        <Tab
          className={classes.tab}
          icon={<SpineIcon />}
          text="Spine"
          active={category === 'spine'}
          onClick={() => setCategory('spine')}
        />
        <Tab
          className={classes.tab}
          icon={<ShoulderIcon />}
          text="Shoulder"
          active={category === 'shoulder'}
          onClick={() => setCategory('shoulder')}
        />
      </section>
      <section className={classes.funcTabs}>
        <ImgText
          className={classes.funcTab}
          img={SummaryImg}
          text="Summary"
          active={func === 'oa'}
          onClick={() => onSwitchFunc('oa')}
        />
        <ImgText
          className={classes.funcTab}
          img={HistoryImg}
          text="History"
          active={func === 'history'}
          onClick={() => onSwitchFunc('history')}
        />
        <ImgText
          className={classes.funcTab}
          img={ThreeDRightImg}
          text="3D-Right"
          active={func === '3d'}
          onClick={() => onSwitchFunc('3d')}
        />
        <ImgText
          className={classes.funcTab}
          img={ThreeDLeftImg}
          text="3D-Left"
          active={func === '3d'}
          onClick={() => onSwitchFunc('3d')}
        />
      </section>
    </div>
  )
}
