import { Grid } from '@material-ui/core'
import StudyList from './components/StudyList'

import Hammer from 'hammerjs'
import dicomParser from 'dicom-parser'
import * as cornerstone from 'cornerstone-core'
import * as cornerstoneMath from 'cornerstone-math'
import * as cornerstoneWADOImageLoader from 'cornerstone-wado-image-loader'
import * as cornerstoneTools from 'cornerstone-tools'
import LayoutPanel from './components/LayoutPanel'
import { DndProvider } from 'react-dnd'
import { HTML5Backend } from 'react-dnd-html5-backend'
import { useCallback, useRef, useState } from 'react'
import Toolbar from 'Toolbar/Toolbar'
import { DEFAULT_LAYOUT, Layout } from 'common/utils'
import { useQuery } from '@apollo/client'
import { ModelFunc, Study, Plugin } from 'common/types'
import { useQueryParams } from 'common/hooks'
import { GET_STUDIES } from './gql'
import { usePatientDetailStyles } from './styles'

function _initCornerstone() {
  // Externals
  cornerstoneWADOImageLoader.external.cornerstone = cornerstone
  cornerstoneWADOImageLoader.external.dicomParser = dicomParser
  cornerstoneTools.external.cornerstoneMath = cornerstoneMath
  cornerstoneTools.external.cornerstone = cornerstone
  cornerstoneTools.external.Hammer = Hammer
  cornerstoneTools.init({
    showSVGCursors: true,
    mouseEnabled: true,
  })
}

_initCornerstone()

export default function PatientDetail() {
  const classes = usePatientDetailStyles()
  const studyIds = useQueryParams('studyIds').split(',')
  const { error, data } = useQuery<{ studies: Study[] }, { ids: string[] }>(GET_STUDIES, {
    variables: { ids: studyIds },
  })
  const [layout, setLayout] = useState<Layout>(DEFAULT_LAYOUT)
  const [currentItemFunc, setCurrentItemFunc] = useState<ModelFunc>('dicom')
  const [currentPlugin, setCurrentPlugin] = useState<Plugin | null>(null)
  const prevLayout = useRef<Layout | null>(null)

  const onSwitchPlugin = useCallback(
    (plugin: Plugin | null) => {
      switch (plugin) {
        case 'fleshRemoval':
          if (currentItemFunc !== 'dicom') return
          break
      }

      // To switch plugin or close
      setCurrentPlugin((prev) => (plugin === prev ? null : plugin))
    },
    [currentItemFunc]
  )

  const backFromFullscreen = useCallback(() => {
    setLayout(prevLayout.current!)
    prevLayout.current = null
  }, [])

  const changeLayout = useCallback(
    (newLayout: Layout) => {
      prevLayout.current = layout
      setLayout(newLayout)
    },
    [layout]
  )

  if (error) {
    return <>'error'</>
  }

  if (!data) {
    return <>'Loading'</>
  }

  return (
    <DndProvider backend={HTML5Backend}>
      <Grid container className={classes.patientDetail} wrap="nowrap">
        <Grid item className={classes.sidebar}>
          <StudyList studies={data!.studies} onOpenAi={() => onSwitchPlugin('ai')} />
        </Grid>
        <Grid item={true} className={classes.main}>
          <Toolbar
            layout={layout}
            onChangeLayout={changeLayout}
            currentPlugin={currentPlugin}
            onSwitchPlugin={onSwitchPlugin}
          />
          <LayoutPanel
            defaultSeries={data.studies[0]?.series[0]}
            layout={layout}
            currentPlugin={currentPlugin}
            updateCurrentFunc={setCurrentItemFunc}
            onChangeLayout={changeLayout}
            resetPlugin={() => onSwitchPlugin(null)}
            backFromFullscreen={backFromFullscreen}
          />
        </Grid>
      </Grid>
    </DndProvider>
  )
}
