import { ReactComponent as BrightnessIcon } from 'assets/icon/brightness.svg'
import { ReactComponent as PanIcon } from 'assets/icon/pan.svg'
import { ReactComponent as MeasurementIcon } from 'assets/icon/measurement.svg'
import { ReactComponent as MagnifierIcon } from 'assets/icon/magnifier.svg'
import IconText from './IconText'
import { useCallback, useEffect, useState } from 'react'
import { globalSubject, SubjectTopic } from 'common/utils'
import * as cornerstoneTools from 'cornerstone-tools'

enum MouseClick {
  Left = 1,
  Right = 2,
  Middle = 4,
}

type XRayToolType =
  | 'Wwwc'
  | 'PanZoom'
  | 'Measurement'
  | 'FleshRemoval'
  | 'Magnify'
  | 'ZoomMouseWheel'

function _initCornerstoneTool() {
  cornerstoneTools.addTool(
    cornerstoneTools.ZoomMouseWheelTool, // Optional configuration
    {
      configuration: {
        invert: false,
        preventZoomOutsideImage: false,
        minScale: 0.1,
        maxScale: 2,
      },
    }
  )
  // addTool: initialize, setToolPassive: being able to view the annotations
  cornerstoneTools.addTool(cornerstoneTools.PanTool)
  cornerstoneTools.setToolPassive('Pan')
  cornerstoneTools.addTool(cornerstoneTools.WwwcTool)
  cornerstoneTools.setToolPassive('Wwwc')
  cornerstoneTools.addTool(cornerstoneTools.MagnifyTool)
  cornerstoneTools.setToolPassive('Magnify')
  cornerstoneTools.addTool(cornerstoneTools.AngleTool)
  cornerstoneTools.setToolPassive('Angle')
  cornerstoneTools.addTool(cornerstoneTools.LengthTool)
  cornerstoneTools.setToolPassive('Length')
}

export default function XrayTool() {
  const [currentTool, setCurrentTool] = useState<XRayToolType | null>(null)

  useEffect(() => {
    const subscription = globalSubject.subscribe(SubjectTopic.InitXrayTool, () => {
      _initCornerstoneTool()
    })
    return () => subscription.unsubscribe()
  }, [])

  useEffect(() => {
    switch (currentTool) {
      case 'Measurement':
        cornerstoneTools.setToolActive('Angle', { mouseButtonMask: MouseClick.Left })
        cornerstoneTools.setToolActive('Length', { mouseButtonMask: MouseClick.Right })
        break
      case 'FleshRemoval':
        // TODO: activate flesh removal
        break
      case 'PanZoom':
        cornerstoneTools.setToolActive('Pan', { mouseButtonMask: MouseClick.Left })
        cornerstoneTools.setToolActive('ZoomMouseWheel', { mouseButtonMask: MouseClick.Left })
        break
      default:
        cornerstoneTools.setToolActive(currentTool as cornerstoneTools.ToolName, {
          mouseButtonMask: MouseClick.Left,
        })
    }
  }, [currentTool])

  const onClickTool = useCallback(
    (tool: XRayToolType) => {
      // Disable current tool
      switch (currentTool) {
        case 'Measurement':
          cornerstoneTools.setToolPassive('Length')
          cornerstoneTools.setToolPassive('Angle')
          break
        case 'FleshRemoval':
          // TODO: cancel flesh removal
          break
        case 'PanZoom':
          cornerstoneTools.setToolPassive('Pan')
          cornerstoneTools.setToolPassive('ZoomMouseWheel')
          break

        default:
          cornerstoneTools.setToolPassive(currentTool!)
      }
      setCurrentTool(tool)
    },
    [currentTool]
  )

  return (
    <>
      <IconText
        icon={<BrightnessIcon />}
        text={`Brightness & Contrast`}
        type="primary"
        active={currentTool === 'Wwwc'}
        onClick={() => onClickTool('Wwwc')}
      />
      <IconText
        icon={<PanIcon />}
        text="Pan & Zoom"
        type="primary"
        active={currentTool === 'PanZoom'}
        onClick={() => onClickTool('PanZoom')}
      />
      <IconText
        icon={<MeasurementIcon />}
        text="Measu-rement"
        type="primary"
        active={currentTool === 'Measurement'}
        onClick={() => onClickTool('Measurement')}
      />
      <IconText
        icon={<MagnifierIcon />}
        text="Magnifier"
        type="primary"
        active={currentTool === 'Magnify'}
        onClick={() => onClickTool('Magnify')}
      />
    </>
  )
}
