import { Grid, GridSize } from '@material-ui/core'
import { useDrop } from 'react-dnd'
import ImageViewer from './ImageViewer'
import ThreeDViewer from './ThreeDViewer'
import History from './History'
import { useCallback, useEffect, useState } from 'react'
import {
  DEFAULT_LAYOUT,
  globalSubject,
  GRIDS,
  Layout,
  parseImageAndInferenceItem,
  SubjectTopic,
} from 'common/utils'
import OASummary from './OASummary'
import { DraggableItem, ModelFunc, OAResult, Plugin, Series } from 'common/types'
import { useLayoutPanelStyles } from 'PatientDetail/styles'
import { ReactComponent as BackspaceIcon } from 'assets/icon/backspace.svg'

const GRID_SIZE: Record<Layout, GridSize> = {
  OneXOne: 12,
  OneXTwo: 6,
  TwoXOne: 12,
  TwoXTwo: 6,
}

function parseSeries(series: Series) {
  const newItems: DraggableItem[] = []
  series?.images.forEach((img) => {
    newItems.push(parseImageAndInferenceItem(img))
  })

  // If current series doesn't fill all the items, fill it with inference result
  if (newItems.length < GRIDS[DEFAULT_LAYOUT]) {
    series?.inference.forEach((inf) => {
      newItems.push(parseImageAndInferenceItem(inf))
    })
  }

  // If current series still have empty block, fill it with null
  if (newItems.length < GRIDS[DEFAULT_LAYOUT]) {
    const emptyItems = new Array(GRIDS[DEFAULT_LAYOUT] - newItems.length).fill(null)
    newItems.push(...emptyItems)
  }

  return newItems
}

const LayoutItem: React.FC<{
  dragIdx: number
  itemId: string
  children: React.ReactElement
  isFocused: boolean
  onDropItem: (item: DraggableItem, targetIdx: number) => void
}> = (props) => {
  const classes = useLayoutPanelStyles()
  const [{ isOver, canDrop }, drop] = useDrop(
    () => ({
      accept: 'box',
      drop: (item: DraggableItem) => {
        props.onDropItem(item, props.dragIdx)
        // return previous item id to notify which item is deactivated
        return { previousItemId: props.itemId }
      },
      collect: (monitor: any) => ({
        isOver: monitor.isOver(),
        canDrop: monitor.canDrop(),
      }),
    }),
    []
  )

  const opacity = isOver && canDrop ? 0.4 : 1
  const border = canDrop ? '1px solid #6EC4D0' : 'unset'

  return (
    <div ref={drop} className={classes.item} style={{ opacity, border }}>
      {props.children}
      {
        // Element without focus should be overlaid by a transparent div to block mouse event
        !props.isFocused && <div className={classes.overlap} />
      }
    </div>
  )
}

interface Props {
  layout: Layout
  defaultSeries: Series
  currentPlugin: Plugin | null
  updateCurrentFunc: (func: ModelFunc) => void
  onChangeLayout: (layout: Layout) => void
  resetPlugin: () => void
  backFromFullscreen: () => void
}

export default function LayoutPanel(props: Props) {
  const {
    layout,
    defaultSeries,
    currentPlugin,
    updateCurrentFunc,
    onChangeLayout,
    resetPlugin,
    backFromFullscreen,
  } = props
  const classes = useLayoutPanelStyles()
  const [focusedIdx, setFocusedIdx] = useState(0)
  const [items, setItems] = useState<DraggableItem[]>(parseSeries(defaultSeries))

  useEffect(() => {
    // Trigger a resize event to re-render 2D & 3D viewer
    window.dispatchEvent(new Event('resize'))
  }, [layout])

  useEffect(() => {
    const subscription = globalSubject.subscribe(
      SubjectTopic.SwitchFunc,
      ({ item }: { item: DraggableItem }) => {
        setItems((prev) => {
          const newItems = [...prev]
          newItems[focusedIdx] = item
          return newItems
        })
        updateCurrentFunc(item.func)
      }
    )

    return () => subscription.unsubscribe()
  }, [focusedIdx, updateCurrentFunc])

  // Handle drag and drop from StudyList
  const onDropItem = useCallback(
    (item: DraggableItem, targetIdx: number) => {
      setItems((prev) => {
        const newItems = [...prev]
        newItems[targetIdx] = item
        return newItems
      })
      updateCurrentFunc(item.func)
    },
    [updateCurrentFunc]
  )

  const onFocusItem = useCallback(
    (idx: number) => {
      if (focusedIdx !== idx) {
        setFocusedIdx(idx)
        updateCurrentFunc(items[idx]?.func)
        // When we focus to other items, flesh removal mode should be disabled
        if (currentPlugin === 'fleshRemoval') {
          resetPlugin()
        }
      }
    },
    [items, updateCurrentFunc, focusedIdx, resetPlugin, currentPlugin]
  )

  const onDoubleClickItem = useCallback(
    (idx: number) => {
      onFocusItem(idx)
      onChangeLayout('OneXOne')
    },
    [onFocusItem, onChangeLayout]
  )

  function renderContent(item: DraggableItem, isFocused: boolean) {
    if (!item) {
      return <>N/A</>
    }
    switch (item.func) {
      case 'dicom':
        const enableFleshRemoval = isFocused && currentPlugin === 'fleshRemoval'
        return (
          <ImageViewer
            id={`xray-${item.id}`}
            filepath={item.file}
            enableFleshRemoval={enableFleshRemoval}
          />
        )
      case '3d':
        return <ThreeDViewer id={`3d-${item.id}`} filepath={item.file} />
      case 'history':
        return <History />
      case 'oa':
        const withThumbnail = layout === 'OneXOne' || layout === 'TwoXOne'
        return <OASummary data={item.data as OAResult} withThumbnail={withThumbnail} />
      default:
        return <>N/A</>
    }
  }

  const gridHeight = layout === 'OneXOne' || layout === 'OneXTwo' ? '100%' : '50%'
  const isFullscreenMode = layout === 'OneXOne'
  return (
    <Grid container className={classes.layoutPanel}>
      {layout === 'OneXOne' && (
        <BackspaceIcon className={classes.backspace} onClick={backFromFullscreen} />
      )}
      {items.map((item, idx) => {
        const isFocused = focusedIdx === idx
        const isVisible =
          (idx < GRIDS[layout] && !isFullscreenMode) || (isFullscreenMode && isFocused)

        if (isVisible) {
          return (
            <Grid
              key={item?.id ?? idx} //use func+id as key to avoid re-render when the layout is changed
              item={true}
              xs={GRID_SIZE[layout]}
              style={{ height: gridHeight }}
              onClick={() => onFocusItem(idx)}
              onDoubleClick={() => onDoubleClickItem(idx)}
            >
              <LayoutItem
                dragIdx={idx}
                itemId={item?.id}
                isFocused={isFocused}
                onDropItem={onDropItem}
              >
                {renderContent(item, isFocused)}
              </LayoutItem>
            </Grid>
          )
        }

        // All the invisible items "should not" be unmounted.
        // Alternatively, we put it out of screen
        return (
          <Grid
            key={item?.id ?? idx}
            item={true}
            xs={GRID_SIZE[layout]}
            style={{ position: 'absolute', top: '0px', right: '100%' }}
          >
            <LayoutItem
              dragIdx={idx}
              itemId={item?.id}
              isFocused={isFocused}
              onDropItem={onDropItem}
            >
              {renderContent(item, isFocused)}
            </LayoutItem>
          </Grid>
        )
      })}
    </Grid>
  )
}
